<template>
  <div
    :class="[
      isEditable ? 'cursor-pointer' : '',
      !selected
        ? 'border border-solid border-stroke-medium bg-bg-neutral-raised'
        : '',
    ]"
    class="h-spacing-200 w-spacing-200 rounded-03"
  >
    <div
      v-if="selected"
      class="flex h-full w-full items-center justify-center rounded-03 p-[2px]"
      :class="
        selected
          ? error
            ? 'bg-constant-status-error'
            : 'bg-constant-brand-primary'
          : ''
      "
    >
      <IconX
        v-if="error"
        class="h-full w-full object-contain text-content-on-neutral-primary"
      />
      <IconCheck
        v-else
        class="h-full w-full object-contain text-content-on-neutral-on-inverse"
      />
    </div>
  </div>
</template>
<script>
export default {
  props: {
    selected: {
      required: false,
      type: Boolean,
      default: false,
    },
    isEditable: {
      required: false,
      type: Boolean,
      default: true,
    },
    customStyle: {
      required: false,
      type: [String, Array],
      default: "",
    },
    error: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  components: {
    IconX: () => import("@/components/svgIcons/x.vue"),
    IconCheck: () => import("@/components/svgIcons/check.vue"),
  },
};
</script>
